.product {
    padding: 20px;
    max-width: 1200px;
    margin: 0 auto;
}

.product-not-found {
    text-align: center;
    padding: 50px;
}

.product-container {
    display: flex;
    gap: 50px;
    margin-top: 30px;
}

.product-left {
    flex: 1;
}

.product-right {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.product-img img {
    width: 100%;
    max-width: 500px;
    border-radius: 10px;
}

.product-prices {
    display: flex;
    gap: 20px;
    align-items: center;
}

.product-price-new {
    font-size: 24px;
    font-weight: 600;
    color: #ff4141;
}

.product-price-old {
    font-size: 18px;
    text-decoration: line-through;
    color: #8c8c8c;
}

.product-sizes h3, .product-quantity h3 {
    margin-bottom: 10px;
}

.size-options {
    display: flex;
    gap: 10px;
}

.size-option {
    padding: 10px 15px;
    border: 1px solid #ebebeb;
    border-radius: 5px;
    cursor: pointer;
}

.size-option.selected {
    background: #ff4141;
    color: white;
    border-color: #ff4141;
}

.quantity-controls {
    display: flex;
    align-items: center;
    gap: 15px;
}

.quantity-controls button {
    width: 30px;
    height: 30px;
    border: 1px solid #ebebeb;
    background: none;
    cursor: pointer;
    border-radius: 5px;
}

.add-to-cart-btn {
    padding: 15px;
    background: #ff4141;
    color: white;
    border: none;
    border-radius: 5px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
}

.add-to-cart-btn:hover {
    background: #e63939;
    transform: translateY(-2px);
}

.add-to-cart-btn:disabled {
    background: #cccccc;
    cursor: not-allowed;
    transform: none;
}

/* Responsive Design */
@media (max-width: 768px) {
    .product-container {
        flex-direction: column;
        gap: 30px;
    }
    
    .product-img img {
        max-width: 100%;
    }
}