.popular {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; /* Centers content vertically */
    gap: 15px;
    min-height: 90vh; /* Uses min-height instead of fixed height for flexibility */
    padding: 40px 20px; /* More generous padding */
    background: linear-gradient(to bottom, #f9f9f9, #ffffff); /* Soft gradient background */
}

.popular h1 {
    color: #171717;
    font-size: 48px;
    font-weight: 600;
    letter-spacing: 1px; /* Slight spacing for elegance */
    margin-bottom: 5px;
    text-align: center;
}

.popular hr {
    width: 180px;
    height: 4px;
    border: none;
    border-radius: 4px;
    background: linear-gradient(90deg, #ff4141, #ff8c8c); /* Gradient for the divider */
    margin: 10px 0 40px; /* Better vertical spacing */
    opacity: 0.8;
    transition: width 0.3s ease; /* Smooth hover effect */
}

.popular:hover hr {
    width: 220px; /* Expands on hover for a subtle interaction */
}

.popular-item {
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
    justify-content: center;
    align-items: flex-start; /* Aligns items to top if heights vary */
    max-width: 1200px; /* Prevents items from stretching too wide */
    margin: 0 auto; /* Centers the grid */
    padding: 20px;
}

/* Optional: Add a smooth fade-in animation for items */
@keyframes fadeIn {
    from { opacity: 0; transform: translateY(20px); }
    to { opacity: 1; transform: translateY(0); }
}

.popular-item > * {
    animation: fadeIn 0.5s ease forwards;
    opacity: 0;
}

/* Delay each item's animation for a cascading effect */
.popular-item > *:nth-child(1) { animation-delay: 0.1s; }
.popular-item > *:nth-child(2) { animation-delay: 0.2s; }
.popular-item > *:nth-child(3) { animation-delay: 0.3s; }
.popular-item > *:nth-child(4) { animation-delay: 0.4s; }
.item {
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.item:hover {
    transform: translateY(-5px); /* Slight lift */
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1); /* Soft shadow */
}
@media (max-width: 768px) {
    .popular {
        height: auto;
        padding: 20px 10px;
    }
    .popular h1 {
        font-size: 40px;
    }
    .popular-item {
        gap: 20px;
    }
}