.footer {
    background: #f8f8f8;
    color: #333;
    padding: 60px 0 20px;
    font-family: 'Poppins', sans-serif;
  }
  
  .footer-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
  }
  
  .footer-logo {
    display: flex;
    align-items: center;
    gap: 15px;
  }
  
  .footer-logo img {
    width: 50px;
    height: auto;
  }
  
  .footer-logo p {
    font-size: 24px;
    font-weight: 700;
    color: #fff;
  }
  
  .footer-links {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 30px;
    list-style: none;
    padding: 0;
  }
  
  .footer-links li a {
    color: #fff;
    text-decoration: none;
    font-size: 16px;
    font-weight: 500;
    transition: color 0.3s ease;
  }
  
  .footer-links li a:hover {
    color: #ff4141;
  }
  
  .footer-social {
    display: flex;
    gap: 20px;
  }
  
  .footer-social a {
    width: 40px;
    height: 40px;
    background: #fff;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    /* box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); */
    transition: transform 0.3s ease;
  }
  
  .footer-social a:hover {
    transform: translateY(-5px);
  }
  
  .footer-social img {
    width: 20px;
    height: 20px;
    object-fit: contain;
  }
  
  .footer-copyright {
    width: 100%;
    text-align: center;
    margin-top: 30px;
  }
  
  .footer-copyright hr {
    border: none;
    height: 1px;
    background: #e3e3e3;
    margin-bottom: 20px;
  }
  
  .footer-copyright p {
    font-size: 14px;
    color: #777;
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .footer-links {
      gap: 15px;
    }
    
    .footer-logo p {
      font-size: 20px;
    }
  }
  @media (prefers-color-scheme: dark) {
    .footer {
      background: #222;
      color: #eee;
    }
  }