.newsletter {
    width: 100%;
    padding: 60px 0;
    background: linear-gradient(180deg, #fde1ff 0%, #e1ffea22 60%);
    text-align: center;
  }
  
  .newsletter-content {
    max-width: 800px;
    margin: 0 auto;
    padding: 0 20px;
  }
  
  .newsletter h1 {
    color: #171717;
    font-size: 2.5rem;
    font-weight: 600;
    margin-bottom: 15px;
  }
  
  .newsletter p {
    color: #454545;
    font-size: 1.2rem;
    margin-bottom: 30px;
  }
  
  .newsletter form {
    display: flex;
    justify-content: center;
    gap: 10px;
    flex-wrap: wrap;
  }
  
  .newsletter input {
    width: 400px;
    max-width: 100%;
    height: 60px;
    padding: 0 20px;
    border: 1px solid #e3e3e3;
    border-radius: 30px;
    font-size: 1rem;
    outline: none;
    transition: all 0.3s ease;
  }
  
  .newsletter input:focus {
    border-color: #ff4141;
    box-shadow: 0 0 10px rgba(255, 65, 65, 0.2);
  }
  
  .newsletter button {
    width: 180px;
    height: 60px;
    border-radius: 30px;
    background: #ff4141;
    color: white;
    font-size: 1rem;
    font-weight: 600;
    border: none;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .newsletter button:hover {
    background: #e63939;
    transform: translateY(-3px);
    box-shadow: 0 5px 15px rgba(255, 65, 65, 0.3);
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .newsletter h1 {
      font-size: 2rem;
    }
    
    .newsletter p {
      font-size: 1rem;
    }
    
    .newsletter form {
      flex-direction: column;
      align-items: center;
    }
    
    .newsletter input,
    .newsletter button {
      width: 100%;
      max-width: 350px;
    }
  }