.new-collections {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    margin-bottom: 100px;
    padding: 0 5%; /* Responsive padding */
}

.new-collections h1 {
    color: #171717;
    font-size: 50px;
    font-weight: 600;
    text-align: center;
}

.new-collections hr {
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background: linear-gradient(90deg, #ff4141, #ff8c8c); /* Gradient effect */
    border: none;
    margin: 20px 0;
}

.collections {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr)); /* Responsive grid */
    gap: 30px;
    margin-top: 50px;
    width: 100%;
    max-width: 1280px;
}