.cartitems {
    margin: 60px auto;
    max-width: 1200px;
    padding: 0 20px;
}

.cartitems-format-main {
    display: grid;
    grid-template-columns: 1fr 2fr 1fr 1fr 1fr 1fr;
    align-items: center;
    gap: 20px;
    padding: 15px 0;
    font-weight: 600;
    color: #454545;
}

.cartitems-divider {
    border: none;
    height: 1px;
    background: #e2e2e2;
}

.cartitems-format {
    display: grid;
    grid-template-columns: 1fr 2fr 1fr 1fr 1fr 1fr;
    align-items: center;
    gap: 20px;
    padding: 15px 0;
    color: #454545;
}

.cartitems-product-icon {
    width: 70px;
    height: 70px;
    object-fit: contain;
    border-radius: 5px;
}

.cartitems-quantity {
    display: flex;
    align-items: center;
    gap: 10px;
}

.cartitems-quantity button {
    width: 30px;
    height: 30px;
    border: 1px solid #ebebeb;
    background: none;
    cursor: pointer;
    border-radius: 5px;
    font-size: 16px;
}

.cartitems-quantity button:hover {
    background: #f5f5f5;
}

.cartitems-remove-icon {
    width: 20px;
    cursor: pointer;
    transition: transform 0.3s;
}

.cartitems-remove-icon:hover {
    transform: scale(1.1);
}

.cartitems-total {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-top: 40px;
    gap: 20px;
}

.cartitems-total h2 {
    font-size: 24px;
    color: #333;
}

.cartitems-checkout {
    padding: 12px 30px;
    background: #ff4141;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s;
}

.cartitems-checkout:hover {
    background: #e63939;
    transform: translateY(-2px);
}

.cartitems-empty {
    text-align: center;
    padding: 60px 0;
}

.cartitems-empty p {
    font-size: 18px;
    color: #666;
    margin-bottom: 20px;
}

.cartitems-continue-shopping {
    padding: 12px 30px;
    background: #333;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    transition: all 0.3s;
}

.cartitems-continue-shopping:hover {
    background: #555;
}

/* Responsive Design */
@media (max-width: 900px) {
    .cartitems-format-main,
    .cartitems-format {
        grid-template-columns: 1fr 1.5fr 1fr 1fr 1fr;
    }
    
    .cartitems-format-main p:nth-child(1),
    .cartitems-format > img {
        display: none;
    }
}

@media (max-width: 600px) {
    .cartitems-format-main,
    .cartitems-format {
        grid-template-columns: 1.5fr 1fr 1fr;
        gap: 10px;
    }
    
    .cartitems-format-main p:nth-child(3),
    .cartitems-format p:nth-child(3) {
        display: none;
    }
    
    .cartitems-product-icon {
        width: 50px;
        height: 50px;
    }
}