.descriptionBox {
    margin: 50px 0;
    max-width: 1200px;
    width: 100%;
}

.descriptionBox-navigator {
    display: flex;
    border-bottom: 1px solid #e0e0e0;
}

.descriptionBox-nav-box {
    padding: 12px 24px;
    font-size: 16px;
    font-weight: 600;
    color: #555;
    cursor: pointer;
    transition: all 0.3s ease;
    border-bottom: 3px solid transparent;
}

.descriptionBox-nav-box:hover {
    color: #ff4141;
}

.descriptionBox-nav-box.active {
    color: #ff4141;
    border-bottom: 3px solid #ff4141;
}

.descriptionBox-content {
    padding: 24px 0;
}

.descriptionBox-description {
    line-height: 1.6;
    color: #555;
}

.descriptionBox-description p {
    margin-bottom: 15px;
}

.descriptionBox-reviews {
    padding: 15px 0;
}

.reviews-average {
    margin-bottom: 20px;
}

.reviews-average h4 {
    color: #333;
    font-size: 18px;
}

.review-item {
    padding: 15px 0;
    border-bottom: 1px solid #f0f0f0;
}

.review-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 8px;
}

.review-user {
    font-weight: 600;
    color: #333;
}

.review-stars {
    color: #ffb800;
    font-size: 16px;
}

.review-comment {
    color: #555;
    line-height: 1.5;
}

.write-review-btn {
    margin-top: 20px;
    padding: 10px 20px;
    background: #ff4141;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: all 0.3s ease;
}

.write-review-btn:hover {
    background: #e63939;
}

/* Responsive Design */
@media (max-width: 768px) {
    .descriptionBox-nav-box {
        padding: 10px 15px;
        font-size: 14px;
    }
    
    .descriptionBox-content {
        padding: 15px 0;
    }
}