.loginsignup {
    width: 100%;
    height: 100vh;
    background: #fce3fe;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .loginsignup-container {
    width: 100%;
    max-width: 500px;
    background: white;
    margin: auto;
    padding: 40px 60px;
    border-radius: 20px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  }
  
  .loginsignup-container h1 {
    margin: 20px 0;
    color: #454545;
    font-size: 28px;
    font-weight: 600;
    text-align: center;
  }
  
  .loginsignup-fields {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin: 30px 0;
  }
  
  .loginsignup-fields input {
    height: 50px;
    padding: 0 20px;
    border: 1px solid #c9c9c9;
    border-radius: 10px;
    outline: none;
    font-size: 16px;
    transition: all 0.3s ease;
  }
  
  .loginsignup-fields input:focus {
    border-color: #ff4141;
    box-shadow: 0 0 5px rgba(255, 65, 65, 0.3);
  }
  
  .loginsignup-container button {
    width: 100%;
    height: 50px;
    background: #ff4141;
    color: white;
    border: none;
    border-radius: 10px;
    font-size: 18px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .loginsignup-container button:hover {
    background: #e63939;
    transform: translateY(-2px);
  }
  
  .loginsignup-login {
    margin-top: 20px;
    color: #5c5c5c;
    font-size: 16px;
    text-align: center;
  }
  
  .loginsignup-login span {
    color: #ff4141;
    font-weight: 600;
    cursor: pointer;
  }
  
  .loginsignup-login span:hover {
    text-decoration: underline;
  }
  
  .loginsignup-agree {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-top: 20px;
    color: #5c5c5c;
    font-size: 14px;
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .loginsignup-container {
      padding: 30px;
    }
    
    .loginsignup-container h1 {
      font-size: 24px;
    }
    
    .loginsignup-fields input {
      height: 45px;
      font-size: 14px;
    }
    
    .loginsignup-container button {
      height: 45px;
      font-size: 16px;
    }
  }