.hero {
    display: flex;
    min-height: 100vh;
    background: linear-gradient(180deg, #fde1ff 0%, #e1ffea22 60%);
    padding: 0 5%;
    align-items: center;
    position: relative;
    overflow: hidden;
}

.hero-left {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 1.5rem;
    z-index: 2;
}

.hero-left h2 {
    color: #090909;
    font-size: clamp(1.5rem, 3vw, 1.8rem);
    font-weight: 600;
    margin: 0;
    animation: fadeIn 1s ease-out;
}

.hero-text-container {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}

.hero-hand-icon {
    display: flex;
    align-items: center;
    gap: 1rem;
    animation: bounce 2s infinite;
}

.hero-hand-icon p {
    color: #171717;
    font-size: clamp(3rem, 8vw, 5rem);
    font-weight: 700;
    margin: 0;
    line-height: 1;
}

.hero-main-text {
    color: #171717;
    font-size: clamp(3rem, 10vw, 6rem);
    font-weight: 700;
    margin: 0;
    line-height: 1;
    animation: slideIn 0.8s ease-out;
}

.hero-sub-text {
    color: #171717;
    font-size: clamp(3rem, 10vw, 6rem);
    font-weight: 700;
    margin: 0;
    line-height: 1;
    animation: slideIn 1s ease-out;
}

.hero-latest-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    width: clamp(15rem, 30vw, 20rem);
    height: 4.5rem;
    border-radius: 4rem;
    background: #ff4141;
    color: white;
    font-size: clamp(1rem, 2vw, 1.2rem);
    font-weight: 500;
    border: none;
    cursor: pointer;
    transition: all 0.3s ease;
    margin-top: 2rem;
    animation: fadeInUp 1s ease-out;
}

.hero-latest-btn:hover {
    background: #e63737;
    transform: translateY(-0.2rem);
    box-shadow: 0 0.5rem 1rem rgba(0,0,0,0.1);
}

.hero-latest-btn img {
    width: 1.2rem;
    transition: transform 0.3s ease;
}

.hero-latest-btn:hover img {
    transform: translateX(0.3rem);
}

.hero-right {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
}

.hero-image {
    width: 100%;
    max-width: 600px;
    height: auto;
    animation: float 3s ease-in-out infinite;
}

/* Animations */
@keyframes fadeIn {
    from { opacity: 0; transform: translateY(1rem); }
    to { opacity: 1; transform: translateY(0); }
}

@keyframes slideIn {
    from { opacity: 0; transform: translateX(-2rem); }
    to { opacity: 1; transform: translateX(0); }
}

@keyframes bounce {
    0%, 100% { transform: translateY(0); }
    50% { transform: translateY(-0.5rem); }
}

@keyframes fadeInUp {
    from { opacity: 0; transform: translateY(2rem); }
    to { opacity: 1; transform: translateY(0); }
}

@keyframes float {
    0%, 100% { transform: translateY(0); }
    50% { transform: translateY(-1rem); }
}

/* Responsive Design */
@media (max-width: 768px) {
    .hero {
        flex-direction: column;
        padding: 5rem 5%;
        text-align: center;
    }
    
    .hero-left {
        align-items: center;
    }
    
    .hero-hand-icon {
        justify-content: center;
    }
    
    .hero-right {
        margin-top: 3rem;
    }
}

@media (max-width: 480px) {
    .hero {
        min-height: auto;
        padding: 3rem 5%;
    }
    
    .hero-latest-btn {
        height: 3.5rem;
    }
}