.productdisplay {
    display: flex;
    margin: 0 auto;
    max-width: 1200px;
    padding: 20px;
    gap: 50px;
}

.productdisplay-left {
    display: flex;
    gap: 20px;
}

.productdisplay-img-list {
    display: flex;
    flex-direction: column;
    gap: 16px;
}

.productdisplay-img-list img {
    height: 120px;
    width: 80px;
    object-fit: cover;
    cursor: pointer;
    border-radius: 5px;
    border: 1px solid #e0e0e0;
    transition: all 0.3s ease;
}

.productdisplay-img-list img.selected-thumbnail {
    border: 2px solid #ff4141;
}

.productdisplay-img {
    flex: 1;
}

.productdisplay-main-img {
    width: 100%;
    max-width: 500px;
    max-height: 600px;
    object-fit: contain;
}

.productdisplay-right {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.productdisplay-right h1 {
    font-size: 28px;
    font-weight: 700;
    color: #333;
}

.productdisplay-right-star {
    display: flex;
    align-items: center;
    gap: 5px;
}

.productdisplay-right-star img {
    width: 20px;
}

.productdisplay-right-star p {
    margin-left: 10px;
    color: #666;
}

.productdisplay-right-prices {
    display: flex;
    gap: 20px;
    align-items: center;
    margin: 10px 0;
}

.productdisplay-right-price-new {
    font-size: 24px;
    font-weight: 600;
    color: #ff4141;
}

.productdisplay-right-price-old {
    font-size: 18px;
    text-decoration: line-through;
    color: #888;
}

.productdisplay-right-description {
    color: #555;
    line-height: 1.5;
    margin: 15px 0;
}

.productdisplay-right-size h2 {
    font-size: 20px;
    font-weight: 600;
    color: #333;
    margin-bottom: 10px;
}

.productdisplay-right-sizes {
    display: flex;
    gap: 15px;
    margin: 10px 0;
}

.size-option {
    padding: 10px 15px;
    border: 1px solid #ebebeb;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.3s ease;
}

.size-option:hover {
    background: #f5f5f5;
}

.productdisplay-right-quantity {
    margin: 20px 0;
}

.productdisplay-right-quantity h2 {
    font-size: 20px;
    font-weight: 600;
    color: #333;
    margin-bottom: 10px;
}

.quantity-controls {
    display: flex;
    align-items: center;
    gap: 15px;
}

.quantity-controls button {
    width: 30px;
    height: 30px;
    border: 1px solid #ebebeb;
    background: none;
    cursor: pointer;
    border-radius: 5px;
    font-size: 16px;
}

.productdisplay-right-button {
    padding: 15px 30px;
    background: #ff4141;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    margin: 20px 0;
}

.productdisplay-right-button:hover {
    background: #e63939;
    transform: translateY(-2px);
}

.productdisplay-right-category {
    color: #666;
    font-size: 14px;
}

/* Responsive Design */
@media (max-width: 900px) {
    .productdisplay {
        flex-direction: column;
        gap: 30px;
    }
    
    .productdisplay-left {
        flex-direction: column-reverse;
    }
    
    .productdisplay-img-list {
        flex-direction: row;
        justify-content: center;
    }
    
    .productdisplay-main-img {
        max-height: 400px;
    }
}

@media (max-width: 500px) {
    .productdisplay-right-sizes {
        flex-wrap: wrap;
    }
    
    .productdisplay-right-button {
        padding: 12px 20px;
    }
}