.related-products {
    padding: 40px 20px;
    max-width: 1200px;
    margin: 0 auto;
    text-align: center;
}

.related-products h1 {
    font-size: 28px;
    font-weight: 600;
    color: #171717;
    margin-bottom: 10px;
}

.related-products-divider {
    width: 200px;
    height: 3px;
    background: #ff4141;
    border: none;
    margin: 0 auto 30px;
}

.related-products-items {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 30px;
    justify-content: center;
}

.no-related-products {
    grid-column: 1 / -1;
    color: #666;
    font-size: 18px;
    padding: 40px 0;
}

/* Responsive Design */
@media (max-width: 900px) {
    .related-products-items {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 500px) {
    .related-products-items {
        grid-template-columns: 1fr;
    }
    
    .related-products h1 {
        font-size: 24px;
    }
}